body {
  position: relative;
}

.MuiStepConnector-alternativeLabel {
  right: calc(-50% + 20px) !important;
  left: calc(50% + 20px) !important;
}

.input-wrapper {
  border: solid 1px black;
  width: 25%;
  margin: auto;
  border-radius: 10px;
  padding: 10px;
}

.input {
  margin-bottom: 1.5rem !important;
  color: black !important;
}

input[type="file" i]  {
  display: block !important;
  margin-right: auto !important;
  width: auto !important;
}

.year {
  text-align: center;
}

.add-date {
  margin-top: 25px;
  margin-left: 10px;
  padding: 0 10px 10px;
  border: black solid 1px;
  border-radius: 7px;
  text-align: center;
  background-color: rgb(235, 235, 235);
}

.add-date h3 {
  margin-top: 0;
}

.add-date-inner {
  align-items: flex-end;
}

.picked-date {
  font-size: 1.3rem;
  padding-right: 10px;
}

.number {
  margin: 9px !important;
}

.position {
  margin-left: 15px !important;
  width: 100px !important;
}

.add-btn {
  margin-right: auto !important;
}

.accordion {
  background-color: #303f9f !important;
  color: #fff !important;
}

.accordion h4 {
  margin: 2px !important;
}

.accordion h4 span{
  font-size: 1.3rem;
  /* text-decoration: underline !important; */
  color: rgb(250, 142, 0);
}

.list .MuiListItemIcon-root {
  text-align: left !important;
  display: block !important;
}

.list .MuiListItemIcon-root svg{
  padding-top: 4px;
}

.list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.list .MuiButtonBase-root {
  align-items: unset !important;
}

.list strong {
  font-size: larger;
  padding-left: 6px;
}
