@font-face {
  font-family: "Alef Hebrew";
  src: url(./fonts/Alef-Regular.ttf);
}

* {
  font-family: "Alef Hebrew" !important;
}

body {
  margin: 0 !important;
}

.MuiTypography-body1 {
  font-family: "Alef Hebrew" !important;
}

.container {
  min-height: 82vh;
}

.spinner-wrapper {
  background-color: #2c2c2c41;
  width: 100%;
  min-height: max(100vh, 100%);
  padding-top: 25%;
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  text-align: center;
  z-index: 1000;
}
