.dedication {
  background-color: #f2f5ff;
  border-radius: 5px;
  border: 1px solid #011746;
  margin: 64px auto;
  padding-bottom: 10px;
}

.dedication .makeStyles-paper-5 {
  margin: 0 !important;
}

.dedication .MuiFormControl-marginNormal {
  margin: 0 !important;
}

.dedication h3 {
  margin-bottom: 0;
}

.ded-obj {
  margin: 0px !important;
}

.dedication h4 {
  margin: 0;
}

.delete {
  text-align: left !important;
}

.notes {
  width: 100%;
}

.modal-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(118, 177, 255, 0.377);
  z-index: 1000;
}

.modal-wrapper:before{
  content: '';
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}

.modal-body {
  display: inline-block;
  padding: 20px 30px;
  background-color: #011746;
  color: #dfe6ff;
  border-radius: 3px;
  position: relative;
}

.close {
  position: absolute;
  top: 0;
  right: 3px;
  font-size: 2rem;
  line-height: 1.4rem;
  height: 1.4rem;
  overflow: hidden;
  cursor: pointer;
}

.close:hover {
  color: #ffffff;
}