@import url(http://fonts.googleapis.com/earlyaccess/alefhebrew.css);


@font-face {
  font-family: Segoe-UI;
  src: url(../../../fonts/segoeui.ttf);
}
.date-wrapper {
  border-bottom: solid 3px #011746;
  position: relative;
}

.date-picker {
  justify-content: space-between !important;
  flex-wrap: nowrap !important;
  background-color: #fff !important;
  padding: 0 auto 0 10%;
  text-align: center;
  width: 95% !important;
  transition: ease 0.5s;
}

.date-picker-extended {
  padding-right: 5%;
  transition: ease 0.5s;
}

.date {
  font-family: "Alef Hebrew", Segoe-UI, Tahoma, Geneva, Verdana, sans-serif, sans-serif !important; 
  padding: 0 20px !important;
  min-height: 32px;
}

.cal {
  position: relative;
}

.reactJewishDatePicker {
  /* position: absolute; */
  top: 0;
  margin-bottom: 0;
  color: transparent;
  border: none !important;
  padding: 0 !important;
}

.selectedDate {
  margin-top: 4px;
  border: none !important;
  padding: 0 !important;
  width: 25px;
  height: 25px;
  overflow: hidden;
  white-space: nowrap;
}

.reactJewishDatePicker .monthWrapper {
  top: 32px !important;
  padding: 7px;
  border: 3px solid #5e5e80 !important;
  border-radius: 5px;
}

.bi-calendar3 {
  position: absolute;
  right: 0px;
  top: 3px;
}

@media (max-width: 560px) {
  .date {
    font-size: 1rem !important;
    padding: 0 5px !important;
  }
}

@media (max-width: 425px) {
  .dateDesc {
    display: block !important;
    text-align: center;
  }

  .nav-icon {
    font-size: 2rem !important;
  }
}