
.cont {
  background-color: #011746;
  min-height: 84vh !important;
  overflow: hidden;
}

.dash {
  min-height: 100% !important;
  position: relative;
  color: aliceblue;
  padding: 0;
  width: 10px !important;
  transition: ease 0.5s;
  flex-basis: 33.3% !important;

}

.dash-ext {
  flex-basis: 0% !important;
  transition: ease 0.5s;
}

.dash h3 {
  text-align: center;
  margin-bottom: 0%;
  display: block;
}

.img {
  background: url(../../../fonts/rabi1.jpg) no-repeat center top / 100%  !important;
  transform: scaleX(-1);
  opacity: 0.11;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  min-height: 84vh !important;
  display: block !important;
  transition: ease 0.5s;
}

.ext-btn {
  position: absolute !important;
  right: -2.5%;
  top: 48%;
  z-index: 100;
  color: #ffffff !important;
  background-color: rgb(25, 79, 180) !important;
  transition: right ease 0.1s !important;
}

.ext-btn-ext {
  position: absolute !important;
  right: 3.3%;
  top: 48%;
  z-index: 1000000;
  color: #ffffff !important;
  background-color: rgb(25, 79, 180) !important;
  transition: right ease 0.3s !important;
}

.dash-btn {
  color: #d3e2ff !important;
  margin-right: 10px !important;
  position: absolute !important;
  bottom: 12% !important;
}

.abst {
  padding: 0 25px;
  text-align: justify;
  font-size: 0.85rem !important;
  opacity: unset !important;
}

.abst p:last-of-type {
  text-align: center;
}

.pdf-wrapper {
  position: relative;
  transition: ease 0.5s;
}

.pdf-wrapper-ext {
  position: relative;
  transition: ease .5s;
  padding-right: 5% !important;
}

.lang-btn {
  position: absolute !important;
  min-width: 10%;
  top: 1% !important;
  left: 6%;
}

#pdf-div {
  width: 100% !important;
}

#iframe-pdf-div {
  width: 95% !important;
  min-height: 84vh;
  margin: auto !important;
  /* display: block; */
}

.pdf {
  width: 95%;
  height: 100%;
}

.share {
  background-color: rgb(157, 205, 250) !important;
  position: fixed !important;
  bottom: 5%;
  left: 0;
  height: 2.5rem;
  width: 2.5rem;
  min-width: unset !important;
  padding: 0 !important;
}

.share svg {
  padding: 11px;
}

.menu .MuiMenu-paper {
  background-color: transparent !important;
  top: unset !important;
  bottom: 11% !important;
  left: 5px !important;
}

.menu .MuiListItem-gutters, .menu .MuiList-padding {
  padding: 0 !important;
}

.menu .MuiPaper-elevation8 {
  box-shadow: unset !important;
}

@media (max-width: 1280px) {
  .cont {
    min-height: unset !important;
    height: 100% !important;
  }

  .dash-btn {
    margin: 15px !important;
    bottom: 0 !important;
    /* position: static !important; */
  }

  .share {
    bottom: max(13%, 85px);
  }

  .menu .MuiMenu-paper {
    bottom: 20% !important;
  }

  .lang-btn {
    top: 0.5% !important;
    left: 3%;
  }

  .ext-btn {
    display: none !important;
  }

  #iframe-pdf-div {
    display: block !important;
    margin: auto !important;
    width: 95% !important;
  }
}