@import url(http://fonts.googleapis.com/earlyaccess/alefhebrew.css);

.nav-bar {
  background-image: linear-gradient(to right, #011746 90%, #01174600 91.5%  ) !important;
  margin: 0 !important;
  position: relative;
  position: absolute;
  padding-right: 10% !important;
  height: 80px !important;
  z-index: 11111;
}

.toolbar {
  height: 80px !important;
}

.bsd {
  font-size: 14px !important;
  top: 7px;
  right: 25px;
  position: absolute;
  font-family: "Alef Hebrew", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.MuiAppBar-positionStatic {
  position: relative !important;
}

.MuiAppBar-colorPrimary {
  background-color: transparent !important;
}

.img-top {
  height: 80px;
  width: 10%;
  display: inline-block;
  background: url(../../fonts/rabitop.jpg) no-repeat center center / cover ;
  position: absolute;
  border-right: #011746 solid 3px;
  right: 0;
  top: 0;
}

.link {
  font-family: "Alef Hebrew", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-decoration: none;
  color: #ffffff !important;
  cursor: pointer;
}

.nav-bar .MuiIconButton-root {
  font-size: 2.5rem !important;
}

.bar-link {
  margin: 0 10px !important;
}

.topic-link {
  color: #1a1a1a !important;
}

.search {
  border: 2px solid #008080 !important;
  color: #008080 !important;
}

.makeStyles-searchIcon-5 {
  color: #008080 !important;
}

.topic-menu .MuiMenu-paper {
  top: 60px !important;
  left: 55px !important;
  z-index: 22222 !important;
}

.MuiList-root {
  margin-right: 0% !important;
}

@media (max-width: 960px) {
  .nav-bar {
    background-image: linear-gradient(to right, #011746 85%, #01174600 86.5%  ) !important;
    padding-right: 13% !important;
  }

  .img-top {
    height: 80px;
    width: 15%;
    background: url(../../fonts/rabitop.jpg) no-repeat center center / cover ;
  }
}

@media (max-width: 415px) {
  .nav-bar {
    background-image: linear-gradient(to right, #011746 85%, #01174600 86.5%  ) !important;
    padding-right: 13% !important;
    height: 70px !important;
  }

  .nav-bar .MuiTypography-h5 {
    margin: 0 !important;
  }

  .nav-bar .MuiIconButton-root {
    padding: 5px !important;
  }

  .img-top {
    height: 70px;
    width: 15%;
    background: url(../../fonts/rabitop.jpg) no-repeat center center / cover ;
  }

  .title {
    font-size: smaller !important;
  }
}